/*
---
name: Link
category:
 - component/typography/link
---

```html
<article class="c-article">
	<p><a href="/DUMMY"></a></li>
	<ul class="c-list--link">
		<li><a href="/DUMMY"><i class="c-link__icon c-list__icon -front fa fa-caret-right"></i></a></li>
		<li><a href="/DUMMY"><i class="c-link__icon c-list__icon -front fa fa-caret-right"></i><i class="c-link__icon -rear fa fa-window-restore"></i></a></li>
		<li><a href="/DUMMY"><i class="c-link__icon c-list__icon -front fa fa-caret-right"></i>PDFPDFPDFPDF(532KB)<i class="c-link__icon -rear fa fa-file-pdf-o"></i></a></li>
	</ul>
</article>
```
*/

a:not([class]) {
	color: $c_main;
	text-decoration: underline;
	&:hover { text-decoration: none; }
}

.c-link__icon {
	@include fz(16);
	vertical-align: 0;
	// 
	&.-front { margin-right: 3px; }
	// 
	&.-rear { margin-left: 5px; }
}